input[type='checkbox'] {
    position: relative;
    width: 120px;
    height: 40px;
    -webkit-appearance: none;
    background: -webkit-gradient(linear, left bottom, left top, from(#333), to(#000));
    background: linear-gradient(0deg, #333, #000);
    outline: none;
    border-radius: 20px;
    box-shadow: 0 0 0 4px #353535, 0 0 0 5px #3e3e3e, 
        inset 0 0 10px rgba(0,0,0,1), 
        0 5px 20px rgba(0,0,0,.5), 
        inset 0 0 15px rgba(0,0,0,.2);
}

input:checked[type='checkbox'] {
    background: -webkit-gradient(linear, left bottom, left top, from(#6efaff), to(#62dadf));
    background: linear-gradient(0deg, #6efaff, #62dadf);
}

input[type='checkbox']:before {
    content: '';
    position: absolute;
    width: 80px;
    height: 40px;
    background: -webkit-gradient(linear, left bottom, left top, from(#000), to(#6b6b6b));
    background: linear-gradient(0deg, #000, #6b6b6b);
    border-radius: 20px;
    box-shadow: 0 0 0 1px #232323;
    -webkit-transform: scale(.98, .96);
            transform: scale(.98, .96);
    -webkit-transition: .5s;
    transition: .5s;
}

input:checked[type='checkbox']:before {
    left: 40px;
}

input[type='checkbox']:after {
    content: '';
    position: absolute;
    top: calc(50% - 2px);
    left: 65px;
    width: 4px;
    height: 4px;
    background: -webkit-gradient(linear, left bottom, left top, from(#6b6b6b), to(#000));
    background: linear-gradient(0deg, #6b6b6b, #000);
    border-radius: 50%;
    -webkit-transition: .5;
    transition: .5;
}

input:checked[type='checkbox']:after {
    background: #97deff;
    left: 105px;
    box-shadow: 0 0 5px #13b3ff, 0 0 15px #13b3ff;
}
